import React, { Component } from 'react'
import { jsx, Canvas, Chart, Line, Axis, Tooltip } from '@antv/f2';
import ReactECharts from 'echarts-for-react';

export default class zhoushi extends Component {
  constructor(props) {
    super(props)
    this.state = {

    }
  }
  async componentDidMount() {


  }
  


  render() {
    
    const option = {
     
      tooltip : {
        trigger: 'axis'
      },
      grid: {
        top: '6%',
        left: '2%',
        right: '2%',
        bottom: '3%',
        containLabel: true
      },
      xAxis : [
        {
          type : 'category',
          boundaryGap : false,
          data : ['7','6','5','4','3','2','1']
        }
      ],
      yAxis : [
        {
          scale:true,
          type : 'value'
        },
   
      ],
      series : [
        {
          type:'line',
          smooth:true,
          symbol: "none",
          lineStyle: {
            color:'#21ba45',
            width: 4,
          },
          data:[1, 0.9, 1.02, 1.05, 1.1, 1.02, 1.01]
        },
      ]
    }
    return (

      <div>
      <ReactECharts 
          option={option}
          theme={'dark'}
          style={{ height: 200 }}
      />;
  </div>


    )
  }
}
